át gạo hàng ngày. ")]),_c('p',{attrs:{"dir":"ltr"}},[_vm._v(" Trong Sống mòn, ta bắt gặp anh Thứ - một thầy giáo đại diện cho tầng lớp tri thức nghèo thời bấy giờ. Anh Thứ đã bỏ lại quê hương và người vợ trẻ để lên Hà thành dạy học trong chính ngôi trường của người anh họ tên Đích với mong ước, hoài bão một cuộc sống tươi đẹp hơn.Thế nhưng, thực tế lại vô cùng khắc nghiệt, cuộc sống chẳng dễ dàng gì với anh cả.")]),_c('p',{staticStyle:{"text-align":"center"},attrs:{"dir":"ltr"}},[_c('em',[_c('img',{staticStyle:{"height":"354px","width":"100%"},attrs:{"alt":"Review Sống Mòn - Nam Cao - Những Cuốn Sách Hay","src":"https://cotich.net/uploads/images/song-mon-nam-cao-4.jpg"}})])]),_c('p',{staticStyle:{"text-align":"center"},attrs:{"dir":"ltr"}},[_c('em',[_vm._v("Đại diện cho tầng lớp trí thức cũng bị vùi dập bởi đói nghèo")])])])]),_c('div',{staticClass:"page scolls",class:_vm.getCl(6),style:(_vm.getZindex(6)),on:{"click":function($event){return _vm.clicks(6)}}},[_c('div',{staticClass:"box-content"},[_c('p',{attrs:{"dir":"ltr"}},[_vm._v(" Dù xa quê bỏ xứ nhưng hoài bão của Thứ lại bị vùi dập bởi những mối lo toan nặng trĩu trên vai. Từ những đối xử ích kỷ của người xung quanh cho đến tiền ăn ở, tiền lương khiến cuộc sống của Thứ trở nên khó khăn, ngột ngạt. Những hy vọng trong lòng Thứ như đang chết dần, chết mòn, hoài bão cũng vì thế mà bỏ đi.")]),_c('p',{attrs:{"dir":"ltr"}},[_vm._v(" Cuộc đời một người tri thức trẻ như anh Thứ lại bị chính cái nghèo đói làm thay đổi. Chính nó là “thủ phạm” khiến Thứ và Liên phải đứt gánh giữa đường. Những hiểu lầm tai hại, uất ức đều là do cái đói khổ gây nên đã phá nát một gia đình, một con người.")]),_c('h2',{attrs:{"dir":"ltr"}},[_vm._v(" Cái nghèo đói nhấn chìm, làm biến dạng tâm hồn cả một cộng đồng")]),_c('p',{attrs:{"dir":"ltr"}},[_vm._v(" Không chỉ A Thứ là nạn nhân bị chết dần tâm hồn do đói khổ mà cả một cộng đồng phải gánh chịu. Dưới mái nhà trọ ẩm thấp, thầy giáo trẻ Thứ biết thêm nhiều người cùng cảnh ngộ.")]),_c('p',{att